.App{
    height: 100vh;
    width: 100%;
}

.main{
    position: absolute;
    height: 40px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    top: -70px;
    right: 0;
    display: flex;
    justify-content: center;

}
.background{
    content: "";
    position: absolute;
    margin-top: 150px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    width: 300px;
    height: 100px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.61);
}

@media screen and (max-width:768px) {
    .main{
        position: absolute;
        height: 40px;
        margin-top: 100px;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        top: -70px;
        right: 0;
        display: flex;
        justify-content: center;

    }
    .background{
        content: "";
        position: absolute;
        margin-top: 100px;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        width: 300px;
        height: 100px;
        border-radius: 20px;
        background-color: rgba(255, 255, 255, 0.61);
    }
}